import ReactTimeAgo from "react-time-ago";
import { Card } from "react-bootstrap";

function Comment({ data }) {
  const uriImg = data.name.split(" ").join("+");
  return (
    <Card className="card">
      <div className="card-top">
        <img
          src={"https://ui-avatars.com/api/?name=" + uriImg}
          alt={data.name}
        />
        <div className="mx-md-3">
          <span className="name">{data.name}</span>
          <span className="time">
            <ReactTimeAgo date={new Date(data.createdAt)} locale="id" />
          </span>
        </div>
      </div>
      <div className="card-bottom">
        <span className="description">{data.wishes}</span>
      </div>
    </Card>
  );
}

export default Comment;
