import { useEffect, useState } from "react";
import Item from "./Item";

function Countdown() {
  function calculateTimeLeft() {
    let year = new Date().getFullYear();
    let difference = +new Date(`09/04/${year}`) - +new Date();
    let timeLeft = {};

    if (difference > 0) {
      timeLeft = {
        day: Math.floor(difference / (1000 * 60 * 60 * 24)),
        hours: Math.floor((difference / (1000 * 60 * 60)) % 24),
        minutes: Math.floor((difference / 1000 / 60) % 60),
        seconds: Math.floor((difference / 1000) % 60),
      };
    } else {
      timeLeft = {
        day: "00",
        hours: "00",
        minutes: "00",
        seconds: "00",
      };
    }

    return timeLeft;
  }

  const [timeLeft, setTimeLeft] = useState(calculateTimeLeft());

  useEffect(() => {
    const timer = setTimeout(() => {
      setTimeLeft(calculateTimeLeft());
    }, 1000);

    return () => clearTimeout(timer);
  });

  return (
    <div data-aos="fade-up" className="countdown">
      <Item time={timeLeft["day"]} text="day" />
      <Item time={timeLeft["hours"]} text="hours" />
      <Item time={timeLeft["minutes"]} text="minutes" />
      <Item time={timeLeft["seconds"]} text="seconds" />
    </div>
  );
}

export default Countdown;
