import { useState } from "react";
import { Row, Col, Button, Modal } from "react-bootstrap";
import { CopyToClipboard } from "react-copy-to-clipboard";

function WalletModal() {
  const [show, setShow] = useState(false);
  const [address, setAddress] = useState({
    value:
      "Abdullah Musa - Komplek Amaya Residence Blok C No. 6 Sukamanah, Kota Bandung 40267",
    copied: false,
  });
  const [digitalWallet, setDigitalWallet] = useState({
    value: "BCA 8470275899 a.n Abdullah Musa",
    copied: false,
  });

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  return (
    <>
      <Button data-aos="zoom-in" variant="secondary" onClick={handleShow}>
        Open Wallet
      </Button>

      <Modal
        show={show}
        onHide={handleClose}
        aria-labelledby="contained-modal-title-vcenter"
        centered
        size="lg"
      >
        <Modal.Body>
          <span className="name mb-4">Send gift to Tien and Musa</span>
          <Row>
            <Col md={6} sm={12} className="mb-3 mb-md-0">
              <span className="description title">Address</span>
              <span className="description text">{address.value}</span>
              <CopyToClipboard
                text={address.value}
                onCopy={() =>
                  setAddress({ value: address.value, copied: true })
                }
              >
                <Button variant="primary">Copy</Button>
              </CopyToClipboard>
            </Col>
            <Col md={6} sm={12} className="mb-3 mb-md-0">
              <span className="description title">Digital Wallet</span>
              <span className="description text">{digitalWallet.value}</span>
              <CopyToClipboard
                text={digitalWallet.value}
                onCopy={() =>
                  setDigitalWallet({ value: digitalWallet.value, copied: true })
                }
              >
                <Button variant="primary">Copy</Button>
              </CopyToClipboard>
            </Col>
          </Row>
        </Modal.Body>
      </Modal>
    </>
  );
}

export default WalletModal;
