import { useState } from "react";
import axios from "axios";
import { Form, Button } from "react-bootstrap";
import { withRouter } from "react-router-dom";

function RsvpForm(props) {
  const [nama, setNama] = useState("");
  const [atten, setAtten] = useState("");
  const [wish, setWish] = useState("");

  function handleSubmit(e) {
    e.preventDefault();

    setNama("");
    setAtten("");
    setWish("");

    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });

    axios
      .post("/api/create", {
        name: nama,
        attendance: atten,
        wishes: wish,
      })
      .then((res) => {
        props.history.push("/" + res.data._id);
      })
      .catch((err) => {
        console.log(err);
      });
  }

  return (
    <Form onSubmit={(e) => handleSubmit(e)}>
      <Form.Group className="mb-3">
        <Form.Control
          type="text"
          name="nama"
          placeholder="Name"
          value={nama}
          onChange={(e) => setNama(e.target.value)}
        />
      </Form.Group>
      <Form.Group className="mb-3">
        <Form.Select
          name="atten"
          id="attendance"
          value={atten}
          onChange={(e) => setAtten(e.target.value)}
        >
          <option value={-1} key={1}>
            Attendance
          </option>
          <option value="Yes" key={2}>
            Yes
          </option>
          <option value="No" key={3}>
            No
          </option>
        </Form.Select>
      </Form.Group>
      <Form.Group className="mb-3">
        <Form.Control
          as="textarea"
          name="wish"
          id="wishes"
          cols={30}
          rows={10}
          placeholder="Wishes"
          value={wish}
          onChange={(e) => setWish(e.target.value)}
        />
      </Form.Group>
      <Button variant="primary" type="submit">
        Send
      </Button>
    </Form>
  );
}

export default withRouter(RsvpForm);
